import React from 'react';
import {link} from 'react-router-dom';
import "./index.css";
import donation1 from "../src/images/donation1.jpg";


class Quotes extends React.Component{
  constructor(props){
    super(props);
    this.state={
        items:[],
        isLoaded: false,
    };
  }

  fetchData(){
      fetch('http://127.0.0.1:8000/model/quotes/')
      .then(response=>response.json())
      .then((json)=>{
          this.setState({
              isLoaded:true,
              items:json,
          });
        
      });
  }
  
  componentDidMount(){
      this.fetchData();

      
      
  }
  render(){
    
    
    var{ isLoaded, items} = this.state;
    if(!isLoaded){
      return <div>loadinfhuhufhru</div>;
    }
    else{
  return(
    <>
  <div>
     <div className="refund_heading">Quotes</div>
     <div className="row refund_content">
       <div className="col-md-9 mx-auto">

     <ul>
          {items.map(item =>(
             <div classname="remove" key={item.quotesid}>

        <div className="Donation1">
          <div>
          
            <img className="Donation_image" src={'http://127.0.0.1:8000' + item.quotesphoto}  />
          </div>
          <div className="Donation_content">
            <b className="donation_heading">{item.quotestitle}</b>
            
          </div>
          </div>


        
           </div>
         ))}
       </ul>

    
       </div>
      </div>
      </div>
     
    </> 
  );


  
    }
}
}

export default Quotes;