import React ,{useState, useEffect} from 'react';
import axios from 'axios';
import Select from 'react-select';
import news1 from "../src/images/news1.jpg";
import news_image1 from "../src/images/ngo-1.jpg";

function Bnews() {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [items, setItems] = useState([]);
  

  // Note: the empty deps array [] means
  // this useEffect will run once
  // similar to componentDidMount()
  useEffect(() => {
    fetch("http://127.0.0.1:8000/model/news/")
      .then(res => res.json())
      .then(
        (result) => {
          setIsLoaded(true);
          setItems(result);
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      )
  }, [])
   
 {
    return (<>
     
     {
         items.slice(Math.max(items.length - 3, 0)).filter(item => item.newsid ).map(item => (
      <>
     <div className="bottom_news">
          <div>
           <a href="./news"><img className="bottom_image" src={'http://127.0.0.1:8000' + item.newsphoto} /></a>
          </div>
          <div className="news_bottom_headline">
            <p className="text" >{item.newstitle}</p>
            <date>{item.newscreatetime}</date>
          </div>
          </div>

          </>   
      ))}

      </>
    );
  }
}
export default Bnews;