import react from 'react';
import "./index.css";


const Refund=()=>{
  return(
    <>
    <div>
      <div className="refund_heading">Refund and Cancellations</div>
      <div className="row refund_content">
        <div className="col-md-9 mx-auto">
          <div>www.jansamvedna.org, reserve the right to refuse/cancel a donation towards the activities:</div>
          <br />
          <ol>
            <li>The donor mentions the purpose of donation.</li>
            <li>The donor does not give his full name, address and other contact details.</li>
            <li>The donor does not give his PAN when donating ₹ 10,000/- or above.</li>
          </ol>
          <br />
          <div>If a person remits donation but due to slow internet connectivity, bank server failure or technical problem of the website, does not receive any confirmation message, and finds that the money has been debited from his / her bank account, please contact us:  jansamvedna.bpl@gmail.com
 We will check the transaction status and update / inform you the status of the transaction.</div>
          <br />
          <div>Please Note:</div>
          <br />
          <ol>
            <li>Refund requests must include the name of the donor and/or transaction number.</li>
            <li>Refunds will be credited back to the original credit card used for payment.</li>
            <li>Refund of donations will not be allowed if the Section 80G Donation Receipts are already communicated / despatched to the donor.</li>
          </ol>
        </div>
      </div>
    </div>
    </>

  );
}
export default Refund;