import React ,{useState, useEffect} from 'react';
import axios from 'axios';
import Select from 'react-select';
import news1 from "../src/images/news1.jpg";

function Lnews() {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [items, setItems] = useState([]);
  

  // Note: the empty deps array [] means
  // this useEffect will run once
  // similar to componentDidMount()
  useEffect(() => {
    fetch("http://127.0.0.1:8000/model/news/")
      .then(res => res.json())
      .then(
        (result) => {
          setIsLoaded(true);
          setItems(result);
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      )
  }, [])
   
 {
    return (<>
     

      <section data-aos="fade-up">
<div className="container ">
{/* <!--Carousel Wrapper*/}
<div id="multi-item-example" className="carousel slide carousel-multi-item" data-ride="carousel">
  {/*<!--Slides*/}
  <div className="carousel-inner" role="listbox">

    {/* <!--First slide*/}
    <div className="carousel-item active">

      <div className="row">

       {
           items.slice(Math.max(items.length - 3, 0)).filter(item => item.newsid ).map(item => (
      
      <>
        <div className="col-md-4 activity_slider">
          <div className="card mb-2">
            <img className="card-img-top" src={'http://127.0.0.1:8000'+ item.newsphoto}
              alt="Card image cap"/>
            <div className="card-body">
              <h4 className="card-title">{item.newstitle}</h4>
              <p className="card-text text">{item.newspost}</p>
              <a href="/News" className="btn ">Read More</a>
            </div>
          </div>
        </div>
      </>   
      ))} 

    </div>
    </div>
  </div>
</div>
</div>
</section>

      </>
    );
  }
}
export default Lnews;