import React ,{useState, useEffect} from 'react';
import axios from 'axios';
import Select from 'react-select';
import quates from "../src/images/quates.jpg";

function Dquotes() {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [items, setItems] = useState([]);
  

  // Note: the empty deps array [] means
  // this useEffect will run once
  // similar to componentDidMount()
  useEffect(() => {
    fetch("http://127.0.0.1:8000/model/quotes/")
      .then(res => res.json())
      .then(
        (result) => {
          setIsLoaded(true);
          setItems(result);
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      )
  }, [])
   
 {
    return (<>


       {
         items.slice(Math.max(items.length - 1, 0)).filter(item => item.quotesid ).map(item => (
      <>
       <div className="col-md-4 mx-auto" data-aos="zoom-out">
          <div className="card mt-5">
            <div className="contact2  tribute_heading">Quotes of The Day</div>
            <img className="slider2" src={'http://127.0.0.1:8000' + item.quotesphoto}
              alt="Card image cap"/>
            <div className="card-body">
              <h4 className="card-title">{item.quotestitle}</h4>
              <p className="card-text text">{item.quotespost}</p>
              <a href="/Quotes" className="btn ">Read More</a>
            </div>
          </div>
          </div>


      </>   
      ))} 

      </>
    );
  }
}
export default Dquotes;