import React ,{useState, useEffect} from 'react';
import axios from 'axios';
import Select from 'react-select';
import news1 from "../src/images/news1.jpg";
import tribute from "../src/images/tribute.jpg";


function Ltribute() {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [items, setItems] = useState([]);
  

  // Note: the empty deps array [] means
  // this useEffect will run once
  // similar to componentDidMount()
  useEffect(() => {
    fetch("http://127.0.0.1:8000/model/tribute/")
      .then(res => res.json())
      .then(
        (result) => {
          setIsLoaded(true);
          setItems(result);
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      )
  }, [])
   
 {
    return (<>


       {
         items.slice(Math.max(items.length - 1, 0)).filter(item => item.tributeid ).map(item => (
      <>
          <div className="col-md-4 mx-auto" data-aos="zoom-out">
          <div className="card mt-5">
          <div className="contact2 tribute_heading">Tribute</div>
            <img className="" src={'http://127.0.0.1:8000' + item.tributephoto}
              alt="Card image cap"/>
            <div className="card-body">
              <h4 className="card-title">{item.tributetitle}</h4>
              <p className="card-text text">{item.tributepost}</p>
              <a className="btn ">Read More</a>
            </div>
          </div>
          </div>


      </>   
      ))} 

      </>
    );
  }
}
export default Ltribute;