import react from 'react';
import "./index.css";
import about_image from "../src/images/about-us.jpg";


const About=()=>{
  return(
    <>
    <div className="row">
  <div className="col-lg-10 mx-auto">
<section className="about_section">
  <div className="row">
  <div className="col-lg-5 mx-auto">
  <div>
  <center><h1 className="about_heading">About </h1>
  <h1 className="about_heading">Jansamvedna</h1></center>
  <p><b>"मैं अकेला ही चला था जानिबे मंज़िल मगर , लोग साथ आते गए और कारवाँ बनता गया "  राधेश्याम अग्रवाल। </b>
<br />
72 बरस के नौजवान पत्रकार दैनिक भास्कर सहित कई अखबारों में रहे। पत्रकारिता ज़िंदा रहे इसके लिए न्यूज़ पोर्टल चला रहे थे। 
बाकी इनकी ज़िन्दगी का मकसद ऐसे नेक काम से जुड़ गया है जिसके लिए आमतौर पर कोई आगे नहीं आता।  13 साल पहले  मुंबई में एक सबअर्बन ट्रैन की चपेट में आने से बचे तो ज़िन्दगी ने जैसे पलटी ही मार दी।  मोहमाया का लालच छोड़ के लग गए लावारिस लाशो के अंतिम संस्कार के काम में।  पहले अकेले चले फिर कुछ हमखयाल दोस्त और अब इनके  मिशन से कोई डेढ़ हज़ार लोग जुड़ चुके है।  इस काम में तमाम लोग बावस्ता हुए तो अग्रवाल साब ने जनसंवेदना नाम की एक संश्था बना ली।  आज पूरे शहर में पुलिस से लेकर किसी भी इंसान को गरीब या लावारिस लाश के आखिरी सफर की तैयारी करनी होती है तो इनसे राब्ता कायम करते है। इतने सालो में 4750 लाशो का अंतिम संस्कार करवा चुके है।  सुबह 8 बजे से लेकर शाम 8 बजे तक ये इस काम में जुटे रहते है।  दोपहर के वक़्त इनकी संस्था हमीदिया अस्पताल में 250 खाने के पैकेट भी बांटती है।  कुल मिलाके ज़िन्दगी की इस शाम को मियाँ खान ने ज़रूरतमंदो की खिदमत में लगा दिया।  यकीनन आपने नेकी कर और दरिया में डाल का जो नमूना पेश किया उससे झूटी शान में लगे लोगो को इबरत लेना चाहिए।  

</p>
</div>
</div>
<div className="col-lg-5 mx-auto">
<img className="about-us d-block w-100" src={about_image} />
<br />
<div>
</div>
</div>
</div>
</section>
</div>
</div>

    </>

  );
}
export default About;